
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

import { GoodsClassList } from "@/servers/api";
import Video from "../common/VideoPlayer";

export default {
directives: {
TransferDom
},
components: {
Popup,
XHeader,
Video,
Sticky
},
data() {
return {
show: true,
leftContent: [],
rightContent: [],
currentIndex: 0,
dataList: []
};
},
mounted() {
this.getClassList();
},
methods: {
getClassList() {
GoodsClassList({})
.then(res => {
if (res.result === 1) {
res.data.list.forEach(element => {
if (element.name === "品类") {
this.dataList = element.son;
element.son.forEach(element => {
this.leftContent.push({ id: element.id, name: element.name });
if (element.son) {
if (element.name === "绘画") {
element.son.forEach(element => {
this.rightContent.push({
id: element.id,
name: element.name
});
});
}
}
});
}
});
}
})
.catch(err => {
console.log(err);
});
},
changeIndex(i, id) {
this.currentIndex = i;
this.rightContent = [];
this.dataList.forEach(item => {
if (item.id === id) {
if (item.son) {
item.son.forEach(item => {
this.rightContent.push({
id: item.id,
name: item.name
});
});
}
}
});
console.log(this.rightContent);
},
hideModel() {
this.$store.commit("showMallLeftOptionState", false);
}
},
computed: {
contentListRightWrap() {
return { height: document.documentElement.clientHeight - 225 + "px" };
},
showModelFlag: {
get: function() {
return this.$store.state.showMallLeftOption;
},
set: function() {}
}
},
watch: {}
};



