import ImageUpload from "@/components/common/UploadImgOne";

export default {
  name: "",
  props: [""],
  data() {
    return {
      imgUrl: "",
      userInfo: {}
    };
  },

  components: {
    ImageUpload
  },

  computed: {},
  created() {
    this.getUserInfo();
  },
  beforeMount() {},

  mounted() {},

  methods: {
    setting() {
      this.$router.push("/setting");
    },
    getHeaderImgUrlData(data) {
      this.imgUrl = data;
    },
    getUserInfo() {
      this.userInfo = this.GLOBAL.getSession("userLoginInfo");
    }
  },

  watch: {},
  updated() {}
};



