import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
import { AdvertiseLoop } from "@/servers/api";
export default {
  props: [""],
  components: {
    swiper,
    swiperSlide
  },
  ready() {},
  methods: {
    getDataList() {
      let imgArr = [];
      AdvertiseLoop({
        type: 1
      })
        .then(res => {
          if (res.result === 1) {
            for (let i = 0; i < res.data.data.carousel.length; i++) {
              imgArr.push({
                img: res.data.data.carousel[i]
              });
            }
            this.swiperSlides = imgArr;
            sessionStorage.removeItem("carouselTip");
            sessionStorage.setItem(
              "carouselTip",
              JSON.stringify(res.data.advert)
            );
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  data() {
    return {
      swiperOption: {
        autoplay: true,
        pagination: {
          el: ".swiper-pagination"
        }
      },
      swiperSlides: []
    };
  },
  computed: {},
  mounted() {
    this.getDataList();
  }
};



